html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}
blockquote,
q {
  quotes: none;
}

blockquote {

  &:before,
  &:after {
    content: '';
    content: none;
  }
}

q {

  &:before,
  &:after {
    content: '';
    content: none;
  }
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.icon {
  display: inline-block;
  font: normal normal normal 16px 'Icon';
  color: inherit;
  flex-shrink: 0;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-sm {
  font-size: 12px;
}
.icon-md {
  font-size: 16px;
}

.icon-lg {
  font-size: 24px;
}
.icon-bg {
  font-size: 32px;
}

@function unicode($str) {
  @return unquote("\"\\#{$str}\"");
}

$icon-double-arrow: unicode(ea02);
$icon-arrow: unicode(ea04);
$icon-twitter: unicode(ea06);
$icon-vimeo: unicode(ea07);
$icon-linkedin: unicode(ea08);
$icon-youtube: unicode(ea09);
$icon-email: unicode(ea0b);
$icon-close: unicode(ea0c);

.icon-double-arrow::before { content: $icon-double-arrow; }
.icon-arrow::before { content: $icon-arrow; }
.icon-twitter::before { content: $icon-twitter; }
.icon-vimeo::before { content: $icon-vimeo; }
.icon-linkedin::before { content: $icon-linkedin; }
.icon-youtube::before { content: $icon-youtube; }
.icon-email::before { content: $icon-email; }
.icon-close::before { content: $icon-close; }


@font-face {
  font-family: 'Icon';
  src: url('/fonts/icons/icons.eot');
  src: url('/fonts/icons/icons.eot') format('embedded-opentype'),
    url('/fonts/icons/icons.woff2') format('woff2'),
    url('/fonts/icons/icons.woff') format('woff'),
    url('/fonts/icons/icons.ttf') format('truetype'),
    url('/fonts/icons/icons.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src:
    local('fonts/roboto'), url("/fonts/roboto/roboto-thin.ttf") format("ttf"),
    local('fonts/roboto'), url("/fonts/roboto/roboto-thin.woff") format("woff"),
    local('fonts/roboto'), url("/fonts/roboto/roboto-thin.woff2") format("woff2");
  font-display: block;
	font-weight: 300;
}

@font-face {
  font-family: "Roboto";
  src:
    local('fonts/roboto'), url("/fonts/roboto/roboto-bold.ttf") format("ttf"),
    local('fonts/roboto'), url("/fonts/roboto/roboto-bold.woff") format("woff"),
    local('fonts/roboto'), url("/fonts/roboto/roboto-bold.woff2") format("woff2");
  font-display: block;
	font-weight: 700;
}
/* COLORS */
$orange: #e48d58;
$blue: #5975bc;
$pink: #eab5de;
$white: #ffffff;
$black: #1c1c1c;

/* LAYOUT */
$max-width: 1440px;

/* SHADOWS */
$box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.5);
@keyframes scroller-pulse {
  0% { transform: translate(0, 0) }
  30% { transform: translate(0, 10px) }
  60%, 100% { transform: translate(0, 0) }
}

@keyframes line-vertical-grow {
	0% { height: 0px }
	100% { height: 80px }
}

@keyframes line-horizontal-grow {
	0% { width: 0px }
	100% { width: 80px }
}
* {
  font-family: 'Roboto', serif;
  line-height: 1;
	font-weight: 300;
  color: $black;
}

@mixin hidden {
	height: 0;
	width: 0;
	visibility: hidden;
}
* {
  margin: 0;
  box-sizing: border-box;
  scrollbar-width: none;
  appearance: none;
}

*::-webkit-scrollbar {
  display: none;
}


.section {
  width: 100%;
	height: 100%;
	min-height: 100vh;
}



.virgin {
	background-color: $pink;	
	display: grid;
  justify-content: center;
  align-content: center;


	&__text {
		color: $white;
		font-size: 25px;
	}
	
	&__cursor, .cursor {
		display: none;
		transition: transform 0.3s ease;
		position: fixed; 
		top: calc(50% + 80px);
		left: 50%;
		transform: translate(-50%, -50%) scale(2);
	
		&__center {
			background-color: $orange;
			width: 16px;
			height: 16px;
			border-radius: 50%;
			position: relative;
			z-index: 10;
		}

		&__leave {
			position: absolute;
			top: 4px;
			left: 12px;
			width: 24px;
			height: 8px;
			border-radius: 50%;
			background-color: $white;
			transform-origin: -4px 4px;
			background-color: white;
			
			&--1 { transform: rotate(0deg) }
			&--2 { transform: rotate(30deg) }
			&--2 { transform: rotate(60deg) }
			&--3 { transform: rotate(90deg) }
			&--4 { transform: rotate(120deg) }
			&--5 { transform: rotate(150deg) }
			&--6 { transform: rotate(180deg) }
			&--7 { transform: rotate(210deg) }
			&--8 { transform: rotate(240deg) }
			&--9 { transform: rotate(270deg) }
			&--11 { transform: rotate(300deg) }
			&--12 { transform: rotate(330deg) }
			&--13 { transform: rotate(360deg) }
			&--14 { transform: rotate(390deg) }
			
		}
	
	}

}